@font-face {
  font-family: themeFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: headerFont;
  src: url('Typographica.ttf');
}

@font-face {
  font-family: postTitleFont;
  src: url('Cascadia.ttf');
}

@font-face {
  font-family: postContentFont;
  src: url('Monoid.ttf');
}


* {
  box-sizing: border-box;
}

html {
    margin: 0;
    padding: 0;
}

body {
 	margin: 0;
    padding: 0;
	font-family: postContentFont;
	background-color: #fae8e0;
	background-repeat: no-repeat;
}

.navbar {
	overflow: auto;
	height: 44px;
	width: 100%;
	box-sizing:border-box;
	background-color: #EF7C8E;
}

.current {
 	color: rgb(70,80,70);

}

.uncurrent {
	color: rgb(240,240,240);
}

#navlink {
	float: right;
	margin-top: 10px;
	margin-right: 10px;
	font-family: headerFont;
}

h1 {
	margin: auto;
}

.postTitle {
		font-family: postTitleFont;
}

.postContent {
		font-family: postContentFont;
}

.headerbar {
	height: 64px;
	width: 100%;
	box-sizing:border-box;
	background-color: #0292B7;
	overflow: hidden;
	color: #DEE2EC;
	text-shadow: 1px 1px #8C756A;
	font-family: headerFont;
}

.leftspacedtitle {
	margin-top: 0px;
	margin-bottom: 10px;
   	padding: 10px;
   	font-family: headerFont;
}

.separator-basic {
	height: 2px;
	width: 100%;
	margin: 0px;
	padding: 0px;
	background-color: #EF7C8E;
}

a {
	color: rgb(70,80,70);
}

.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

[class*="col-"] {
  float: left;
  padding: 15px;
  overflow: hidden;
}

.savedextra {
	  border: 2px solid #EF7C8E;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}


